/* src/css/Loader.css */

.loader-container {
    background-color: #ebeef1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
  }
  
  .loader-text {
    margin-bottom: 10px;
    font-size: 16px;
    color: #333;
  }
  
  .circle-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
  
  .circle {
    width: 10px;
    height: 10px;
    margin: 5px;
    border-radius: 50%;
    background-color: #333;
    animation: undulating-circle 0.6s infinite alternate;
  }
  
  .circle:nth-child(1) { animation-delay: 0s; }
  .circle:nth-child(2) { animation-delay: 0.1s; }
  .circle:nth-child(3) { animation-delay: 0.2s; }
  
  @keyframes undulating-circle {
    0% {
      opacity: 1;
      transform: translateY(0) scale(1);
    }
    50% {
      opacity: 0.5;
      transform: translateY(20px) scale(0.6);
    }
    100% {
      opacity: 1;
      transform: translateY(0) scale(1);
    }
  }
  
  .popup-loader {
    background-color: #ebeef1;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1000;
  }
  